<template>


<div class="container mt-3">

    <!-- Modal agregar noticia -->
    <b-modal v-model="modalShow"
            centered
            size="md"
            title="Noticias"
            :header-bg-variant="headerBgVariant"
            :header-text-variant="headerTextVariant">
        <b-container fluid class="container">
            <div class="col-12">
				<strong><label>Titulo</label></strong>
                <input v-model="noticia.titulo"
                        v-selectonfocus
						type="text"
						class="form-control"/>
            </div>

			<div class="col-12">
                <strong><label>Descripción</label></strong>
                <!-- v-model="" -->
				<textarea v-model="noticia.descripcion"
							class="form-control"
							rows="2"/>
            </div>

            <div class="form-group col-12">
                <label class="control-label">Seleccione una imagen</label>
                <input type="file" ref="file"  class="form-control-file" accept=".jpg,.jpeg,.png, .webp">
            </div>

        </b-container>

        <template #modal-footer>
          <div class="w-100">

			<b-button @click="modalShow = false"
						variant="danger"
						class="float-right"> 
              Cancelar
            </b-button>
            <b-button @click="GuardarNoticia"
						variant="primary"
						class="float-right mr-2">
              Aceptar
            </b-button>

			
          </div>
        </template>

    </b-modal>

    <!-- Modal vista previa -->
    <b-modal v-model="modalPreview"
            centered
            size="md"
            title="Vista Previa"
            :header-bg-variant="headerBgVariant"
            :header-text-variant="headerTextVariant">
            
        <b-container fluid class="container">

            <template>
                <img class="d-block img-fluid w-100"
                    width="200" height="200"
                    :src="'data:image/jpeg;base64,'+noticia.imagen"
                    alt="image slot">
            </template>

        </b-container>

        <template #modal-footer>
            <div class="w-100">
                <b-button @click="modalPreview = false"
                            variant="primary"
                            class="float-right mr-2">Aceptar
                </b-button>
          </div>
        </template>

    </b-modal>

    <div class="mt-3">
        <h3><i class="fas fa-newspaper"></i> Noticias</h3>
        <hr>
    </div>

    <!-- User Interface controls -->
    <b-row class="my-2">
        <!-- FALTA VERIFICAR COMO TOMAR LOS PERMISOS -->
        <b-col class=" text-center col-md-9"/>
        <b-col class=" text-center col-md-3">
            <b-button @click="NuevaNoticia()"
                    variant="primary"
                    class="btn btn-sm py-1 mt-1 btn-block">
                <i class="fa fa-plus"></i> Nuevo
            </b-button>
        </b-col>
    </b-row>

    <!-- Main table element -->
    <b-table small
            stacked="md"
            head-variant="dark"
            :hover="hover"
            :striped="striped"
            :bordered="bordered"
            :items="items"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :filterIncludedFields="filterOn"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            fixed
            @filtered="onFiltered">

        <template v-slot:cell(acciones)="row" >
            <b-button size="sm" variant="primary" @click="vistaPrevia(row.item.idNoticias)"><i class="fas fa-newspaper"></i> Vista Previa</b-button>
            <b-button size="sm" variant="info" @click="editarNoticia(row.item)" class="ml-2" ><i class="fas fa-edit"></i> Editar</b-button>
            <b-button size="sm" variant="success" v-if="row.item.activado" @click="desactivar(row.item.idNoticias)" class="ml-2"><i class="fas fa-eye"></i> Mostrando</b-button>
            <b-button size="sm" variant="danger" v-else @click="activar(row.item.idNoticias)" class="ml-2"><i class="fas fa-eye"></i> Sin mostrar</b-button>
        </template>

    </b-table>
        
    <b-pagination v-model="currentPage"
                    class="mt-1"
                    :total-rows="totalRows"
                    :per-page="perPage"
                    align="center"
                    aria-controls="my-table">
    </b-pagination>
    
    
</div>

</template>

<script>

import servicio from "@/services/servicio-noticias";

export default {
    data(){

        return{
            items: [],
            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],
            fields: [
                { key: "titulo", label: "Titulo", sortable: true, sortDirection: "desc", },
                { key: "descripcion", label: "Descripción", sortable: true, sortDirection: "desc", },
                { key: "acciones", label: "", sortable: true, sortDirection: "desc", },
            ],
            modalShow: false,
            headerBgVariant: "dark",
            headerTextVariant: "light",

            noticia: {
                idNoticias: 0,
				titulo: "",
				descripcion: "",
				imagen: "",
			},

            modalPreview: false,
           
        }
    },
    components: {
        // TableSortable,
    },
    async mounted() {
        await this.ObtenerNoticias();
        await this.CargarRenglones();
    },
    created() {
    },
    computed: {
        sortOptions() {
            // Create an options list from our fields
            return this.fields
                        .filter((f) => f.sortable)
                        .map((f) => {
                            return { text: f.label, value: f.key };
                        });
        },
    },
    methods: {

        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },

        async ObtenerNoticias() {
            this.$loading(true);
            await
            servicio.obtenerNoticias()
                    .then((r) => {
                        console.log('R', r);
                        this.items = r.data;
                        this.$loading(false);
                    })
                    .catch((error) => {
                        console.log(error);
                    });
        },

        CargarRenglones() {
            
                this.totalRows = this.items.length;
           
        },

        NuevaNoticia() {
            this.noticia = {
                idNoticias: 0,
                titulo: "",
                descripcion: "",
                imagen: "",
            };
            this.modalShow = true;
        },

        async GuardarNoticia() {
            const archivosPermitidos = ["image/jpeg","image/png"];
            const file = this.$refs.file.files[0];

            if (!file || archivosPermitidos.indexOf(this.$refs.file.files[0].type) == -1) {
                // this.archivo = false;

                if(this.noticia.idNoticias == 0) {
                    await
                    this.$swal.fire({
                        title: 'ATENCIÓN',
                        text: "Debe de seleccionar una imagen para poder guardar la noticia.",
                        icon: 'warning',
                    })
                    return;
                }
                
            }
            
            this.$loading(true)

            await
            this.$swal.fire({
                title: 'ATENCIÓN',
                text: "¿Desea agregar una nueva noticia?",
                icon: 'question',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Si',
                cancelButtonText: 'Cancelar'
            }).then((result) => {                
                if (result.value) {

                    let formData = new FormData();
                    formData.append('titulo', this.noticia.titulo);
                    formData.append('descripcion', this.noticia.descripcion);
                    formData.append('idNoticias', this.noticia.idNoticias);
                    formData.append('file', file);
                    
                    servicio.guardarNoticia( formData )
                    .then((response) => {
                        console.log(response);
                        this.modalShow = false;
                        this.$swal.fire({
                            title: "¡ATENCIÓN!",
                            text: "Se guardo correctamente la noticia.",
                            icon: "success"
                        });
                        this.ObtenerNoticias();

                        this.noticia = {
                            titulo: "",
                            descripcion: "",
                            imagen: "",
                        };
                        
                    })
                    .catch(error => {
                        this.$loading(false)
                        console.error(error);
                    })
                    .finally(() => {
                        this.$loading(false)
                    });
                  
                }
            })
            await this.ObtenerNoticias();
            await this.CargarRenglones();
        },
        async desactivar(id){
            let noticia = {
            idNoticias : id,
            activado :0
            }
            await servicio.desactivarNoticia(noticia).then((r) => {
                this.ObtenerNoticias();
                this.CargarRenglones();
                console.log(r)
            })
        },
        async activar(id){
            let noticia = {
            idNoticias : id,
            activado : 1
            }
            await servicio.desactivarNoticia(noticia).then((r) => {
                this.ObtenerNoticias();
                this.CargarRenglones();
                console.log(r)
            })
        },
        async vistaPrevia(idNoticias) {
            this.$loading(true);
            await servicio
                .obtenerImagen(idNoticias)
                .then((r) => {
                    console.log(r.data.noticia[0].imagen)
                    this.noticia.imagen = r.data.noticia[0].imagen
                    // this.noticia.imagen = item.imagen;
                    this.modalPreview = true; 
                    
                })
            this.$loading(false);
        },

        async editarNoticia(item) {
            this.noticia = {
                idNoticias: item.idNoticias,
                titulo: item.titulo,
                descripcion: item.descripcion,
                imagen: "",
            };
            console.log(this.noticia)
            this.modalShow = true;
            await this.ObtenerNoticias();
            await this.CargarRenglones();
        }

    }
}
</script>

<style scoped>

    

</style>






