import base from '@/services/servicio-base'
// import axios from 'axios';

const servicio = {}

//............................................................ 
//                      N O T I C I A S
//............................................................

servicio.obtenerNoticias = () => {
    return base.get(`/noticias/todas`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.obtenerImagen = (idNoticias) => {
    return base.get(`/noticias/noticia/${idNoticias}`)
    .then(r => {
        return r.data;
    })
    .catch(error => {
        return error;
    })
}


servicio.desactivarNoticia = (noticia) =>{
    return base.post("/noticias/desactivar", noticia, {
        headers:{
            'Content-Type': 'application/json'
        }
    })
    .then(r => {
        return r.data
    })
    .catch(err => {
        console.log(err)
    })
}

servicio.guardarNoticia = (formData) => {
    return base.post('/noticias/registrarNoticia/', formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

export default servicio